const STORAGE_KEY = "toDoItmes"
export default{
  fetch(){
    return JSON.parse(localStorage.getItem(STORAGE_KEY))
  },
  save(value){
    localStorage.setItem(STORAGE_KEY,JSON.stringify(value))
  },
  delete( ){
    localStorage.removeItem(STORAGE_KEY)
  }
}



