import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


export default new Vuex.Store({
  state:{
    //article  list 页面数据
    article:{
      _id:'',
      title:'',
      author:'Jekyll',
      content:'',
      describe:'just for record',
      create_time:'',
      label:'html',
      alter_time:'',
      type:'原创',
      access_time:1
    },
    //article list page information
    pageInfo:{
      currentPage:1,
      pageSize:15
    },
    //topbar内tag
    topbarTags:[],
    curTag:''
  },
  mutations:{
    edit(state,editArticle){
      state.article = editArticle
    },
    updateTitle(state,nTitle){
      state.article.title = nTitle
    },
    update(state,value){
      state.article.content = value
      state.article.alter_time = new Date()
    },
    updateCurTag(state,tag){
      state.curTag = tag
    },
    addTag(state,tag){  //添加顶部标签
      if(state.topbarTags.indexOf(tag)===-1){
          state.topbarTags.push(tag)
      }
    },
    removeTag(state,tag){  //添加顶部标签
      let index=state.topbarTags.indexOf(tag)
      if(index!==-1){
        state.topbarTags.splice(index,1)
      }
    }
  }
})



