import Vue from 'vue'
import Router from 'vue-router'
import Article from '../components/router-pages/article.vue'
import Portfolio from '../components/router-pages/portfolio.vue'
import Projects from '../components/router-pages/projects.vue'
import Visit from '../components/router-pages/visits.vue'
import About from '../components/router-pages/about.vue'
import Contact from '../components/router-pages/contact.vue'

import Archive from '../components/archive/archive.vue'
import markEditor from '../components/Editor/markeditor.vue'
import articlePage from '../components/article-page/article-page.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Article',
      component: Article,
      children:[
        {
          path: '/',
          name: 'articlePage',
          component: articlePage,
        },
        {
          path:'lists',
          component:Archive
        },
        {
          path:'editor',
          component:markEditor
        }
      ]
    },
    {
      path: '/article',
      name: 'Article',
      component: Article,
      children:[
        {
          path: '/',
          name: 'articlePage',
          component: articlePage,
        },
        {
          path:'lists',
          component:Archive
        },
        {
          path:'editor',
          component:markEditor
        }
      ]
    },
    {
      path: '/portfolio',
      name: 'Portfolio',
      component: Portfolio
    },
    {
      path: '/projects',
      name: 'Projects',
      component:Projects
    },
    {
      path: '/visit',
      name: 'Visit',
      component: Visit
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact
    },

  ]
})



