/*
  name:ToDOList
  version:1.0
  @author:1162882015@qq.com
  created:09/24/2017
*/
<template>
  <div id="todo-list">
    <div class="todo-list-header">
      <input type="text" placeholder="Add Task" autocomplete="off" @keyup="addTask">
    </div>
    <div class="todo-list-content">
      <ul>
        <li v-for="(item,index) in items" :class="{completed:item.completed}" v-show="showItem(item.completed)">
          <div class="td"><input type="checkbox" v-model="item.completed" @change = changeStatus($event,index)>
            {{item.title}}</div>
          <div><i class="el-icon-close" @click="removeItem" :data-index = 'index'></i></div>
        </li>
      </ul>
    </div>
    <div class="todo-list-status">
      <div class="item-count">
        {{items.length}} items left
      </div>
      <div class="choseGroup">
          <label :class="[{active:picked=='all'},'group-item']"><input type="radio"  value="all" v-model="picked">All</label>
          <label :class="[{active:picked=='active'},'group-item']" ><input type="radio"  value="active" v-model="picked">Active</label>
          <label :class="[{active:picked=='completed'},'group-item']"><input type="radio"  value="completed" v-model="picked">Completed</label>
      </div>
    </div>
  </div>

</template>

<script>
import LocalStore from '../../common/js/localStore'

    export default {
        data() {
            return {
              items:[
                {
                  title:'吃饭',
                  completed:true
                },{
                  title:'睡觉',
                  completed:false
                },{
                  title:'打豆豆',
                  completed:false
                },{
                  title:'吃饭',
                  completed:true
                },{
                  title:'睡觉',
                  completed:false
                },{
                  title:'打豆豆',
                  completed:false
                }

              ],
              picked:'all'
            }
        },
        computed:{
          allItems(){
            return this.items
          }
        },
      watch:{
          items:{
            handler:(curVal,oldVal)=>{
              LocalStore.save(curVal)
            },
            // 深度观察
            deep:true
          }
      },
      mounted(){
        var localItems =  LocalStore.fetch()
        if(localItems){
          this.items = localItems
        }
      },
      methods: {
        showItem(val){
          if(this.picked=='all'){
            return true
          }else if(this.picked=='active'&&!val){
            return true
          }else if(this.picked=='completed'&&val){
            return true
          }
        },
        removeItem(e) {
          this.items.splice(e.target.dataset.index, 1)
        },
        addTask(e) {
          if (e.keyCode === 13) {
            this.items.unshift({
              title: e.target.value,
              completed: false
            })
            e.target.value = ''
          }
        }
      }

    }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>
  #todo-list
    padding 16px
    height 388px
  .el-icon-close
    margin-right 4px
  .todo-list-header
    border-bottom 1px solid #a7a7a7
    padding 2px
    display: flex
    input
      display: block
      height 28px
      width 100%
      font-size 24px
      border 0px
      border-radius 4px
      outline none


  .todo-list-status
    font-size 14px
    color #a7a7a7
    display: flex
    margin-top 10px
    .item-count
      padding 4px
    .choseGroup
      label
        float left
        padding 4px
        margin-left 4px
        input
          display: none
      .group-item.active
        background-color: #b3b3b3
        color #e7e7e7
        border-radius 3px



  .todo-list-content
    height 290px
    overflow-y auto
    ul
      li
        display: flex
        justify-content space-between
        border-bottom 1px solid #ddd
        padding 8px
        font-size 16px
        &.completed
          color #ccc
          text-decoration: line-through
        .td
          overflow hidden

</style>



