<template>
  <div id="article-page">
      <div class="btns">
        <div class="lists-page btn-item">
          <router-link to="/lists">
            <div @click="addTag" data-name="lists" class="btn-item-box">
                <div class="box-icon">
                  <svg class="icon icon-list" aria-hidden="true">
                    <use xlink:href="#icon-list"></use>
                  </svg>
                </div>
                <div class="box-letter">
                  <div>Article Lists</div>
                  <div><span>total:16</span></div>
                </div>
            </div>
          </router-link>
        </div>
        <div class="editor-page btn-item" >
          <router-link to="/editor">
            <div @click="addTag" data-name="editor" class="btn-item-box">
                <div class="box-icon">
                  <svg class="icon icon-add" aria-hidden="true">
                    <use xlink:href="#icon-add"></use>
                  </svg>
                </div>
                <div class="box-letter">
                  <div>New Article</div>
                  <div><span>markdown</span></div>
                </div>
            </div>
          </router-link>
        </div>
        <div class="site-visits btn-item" >
          <router-link to="/visit">
            <div @click="addTag" data-name="visits" class="btn-item-box">
              <div class="box-icon">
                <svg class="icon icon-visit" aria-hidden="true">
                  <use xlink:href="#icon-visit"></use>
                </svg>
              </div >
              <div class="box-letter">
                <div>Site visits</div>
                <div><span>total:66</span></div>
              </div>
            </div>
          </router-link>
        </div>
      </div>
      <div class="simple-modules">
        <div class="simple-module-item calendar">
          <div class="module-header">readme</div>
          <div class="bg">
            <h2>个人博客后台管理</h2>
            <hr/><br>
            <p>基于Vue全家桶及Element UI的简便管理系统</p>
            <br><br>
            <h3>背景</h3><br>
            <p>在阿里云上部署一个个人博客，为了便文章的编辑及添加，做了一个简易的管理系统。</p><br>
            <h3>使用技术</h3><br>
            <p>Vue全家桶，<a href="https://github.com/hinesboy/mavonEditor">mavonEditor</a> 插件，<a href="https://github.com/ElemeFE/element">Element UI</a>组件。</p>
            <p> 服务端使用Node.js+MongoDB实现了登录注销、文章的增删改功能。另外已有初步设计但还没实现的文件及图片上传、访问统计，待逐步添加。</p>
          </div>
        </div>
        <div class="simple-module-item todo">
          <div class="module-header"><i class="el-icon-date"></i> todo</div>
          <div>
            <to-do></to-do>
          </div>
        </div>

      </div>
  </div>
</template>

<script>
import ToDo from './todo.vue'
    export default {
        data() {
            return {}
        },
      components:{
        ToDo
      },
      methods: {
        addTag(e) {
          this.$store.commit('addTag', e.currentTarget.dataset.name)
          this.$store.commit('updateCurTag', e.currentTarget.dataset.name)
        }
      }
    }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>
  .btns
    margin-top 32px
    display: flex
    justify-content center
  .btn-item-box
    display: flex
    padding 16px 16px
    border 1px solid #f7f7f7
    background-image:linear-gradient(to bottom, #f1f1f1,#e3e3e3)
    color #444
    border-radius 4px
    margin  0 16px
  a
    text-decoration none
  .icon
    width 40px
    height 40px
    fill #aaa
  .box-letter
    margin-left 8px
    display: flex
    flex-direction column
    justify-content space-between
    span
      font-size 12px
  .simple-modules
    margin-top 32px
    display: flex
    justify-content center
    >div
      border-radius 4px
      height 420px
      background-color: #f7f7f7
      margin 10px 40px
    .calendar
        width 600px
    .todo
        width 320px
    .module-header
      background-image:linear-gradient(to bottom, #f1f1f1,#e3e3e3)
      font-size 16px
      color #666
      padding 10px
    .bg
      padding 32px
</style>



