<template>
  <div id="app">
    <sideBar></sideBar>
    <div class="main-container">
      <topBar></topBar>
      <Panel></Panel>
    </div>

  </div>
</template>

<script>
  import topBar from './views/topbar/topbar.vue'
  import sideBar from './views/sidebar/sidebar.vue'
  import  Panel from './views/panel/panel.vue'
export default {
  name: 'app',
  components:{
    topBar,
    sideBar,
    Panel
  },
//  created:function () {
//     console.log(this.$store.state.article)
//    }

}
</script>

<style  lang="stylus" rel="stylesheet/stylus">
  @import "common/stylus/index.styl"
  #app
    height 100%
    display: flex
    overflow hidden
    .main-container
      display: flex
      flex-direction column
      width:100%
      background-color: #eee
  .icon
    width 18px
    height 18px
    fill #aaa

</style>



