<template>
  <div id="tag">
    <router-link :to="tagName">
     <span @click="updateCurTag" :data-name="tagName">{{tagName}}</span>
    </router-link>
      <i class="el-icon-circle-cross" @click="removeTag" :data-name="tagName"></i>
  </div>

</template>

<script>

    export default {
      props:['tagName'],
        data() {
            return {}
        },
      methods:{
        removeTag(e){
          this.$store.commit('removeTag',e.target.dataset.name)
        },
        updateCurTag(e){
          this.$store.commit('updateCurTag',e.target.dataset.name)
        }
      }
    }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>
  #tag
    border-radius 4px
    color #fff
    font-size 14px
    background-color #8391a5
    padding 5px 5px
    margin-left 8px
    flex 0 0 auto
    a
      color inherit
      text-decoration none
    &.active
      background-color rgba(32,160,255,.1)
      border 1px solid rgba(32,160,255,.2)
      color #20a0ff;
</style>



