<template>
  <div id="archive">
    <div class="archive-table">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column fixed type="selection" width="55">

        </el-table-column>

        <el-table-column label="创建日期" width="180">
          <template scope="scope">
            <el-icon name="time"></el-icon>
            <span style="margin-left: 10px">{{ scope.row.create_time }}</span>
          </template>
        </el-table-column>

        <el-table-column label="标题" width="180">
          <template scope="scope">
            <a href="#">{{ scope.row.title }}</a>
          </template>
        </el-table-column>

        <el-table-column label="文章描述" width="280">
          <template scope="scope">
            <span style="margin-left: 10px">{{ scope.row.describe }}</span>
          </template>
        </el-table-column>
        <el-table-column label="访问数" width="100">
          <template scope="scope">
            <span style="margin-left: 10px">{{ scope.row.access_time }}</span>
          </template>
        </el-table-column>
        <el-table-column label="类型" width="80">
          <template scope="scope">
            <span style="margin-left: 10px">{{ scope.row.type }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="140">
          <template scope="scope">
            <router-link to="/editor"><el-button
              size="small"
              @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            </router-link>

            <el-button
              size="small"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>

        <el-table-column prop="tag" label="标签"
                         :filters="labels"
                         :filter-method="filterLabel"
                         filter-placement="bottom-end">
          <template scope="scope">
            <el-tag
              :type="scope.row.label === 'html' ? 'primary' : 'success'"
              close-transition>{{scope.row.label}}</el-tag>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="archive-page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pageInfo.currentPage"
          :page-sizes="[10, 15, 20, 25]"
          :page-size="pageInfo.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
    </div>
    <div class="archive-footer"></div>
  </div>
</template>

<script>

    export default {
        data() {
          return {
            total:800,
            tableData: [
              {id:'1',
                title:'first',
                author:'Jekyll',
                content:'# 第一篇文章',
                describe:'first issue',
                create_time:'2016-05-02',
                label:'html',
                alter_time:'',
                type:'原创',
                access_time:0
              }
            ],
            labels:[
              { text: 'html', value: 'html' },
              { text: 'js', value: 'js' }
            ]
          }
        },
      components:{

      },
      computed:{
          pageInfo(){
            return this.$store.state.pageInfo
          }
      },
      mounted(){
          this.getData({
            currentPage:this.pageInfo.currentPage,
            pageSize:this.pageInfo.pageSize
          })
      },
      methods: {
        //拉取数据
        getData(pageInfo){
          var url = ''
          this.$http({
            method:'get',
            url:'articles',
            baseURL: 'http://localhost:8080/admin/article/archive',
            params:pageInfo,
          }).then(
            //返回数据需包含，根据pageSize及currentPage筛选后的数据，令包含total
            res=> this.tableData = res.data)
        },
        //表格
        handleEdit(index, row) {
          this.$store.commit('edit',this.tableData[index])
        },
        handleDelete(index, row) {
          alert('当前用户暂无权限！')
//           console.log(index, row);
//           console.log(row.id)
//          var url = ''
//          this.$http({
//            method:'delete',
//            url:'delete',
//            baseURL: 'http://localhost:8080/',
//            params:'id = row.id',
//          }).then(
//            //判断是否删除成功，用来移除本地数据
//            res=> this.tableData = res.data)
        },
        filterLabel(value, row) {
          return row.label === value;
        },
        //分页
        handleSizeChange(val) {
          this.pageInfo.pageSize=val
          //console.log(`每页 ${val} 条`);
          this.getData({
            currentPage:this.pageInfo.currentPage,
            pageSize:this.pageInfo.pageSize
          })
        },
        handleCurrentChange(val) {
          console.log(`当前页: ${val}`);
          this.getData({
            currentPage:this.pageInfo.currentPage,
            pageSize:this.pageInfo.pageSize
          })
        }
      }
    }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>
  .archive-page
    padding-left 20%
    margin-top 32px
</style>



