<template>
  <div id="topbar">
      <div class="topbar-container">
        <div class="tag-area">
          <div  v-for="(item,index) in tags">
            <topbar-tag :tagName="item" :class="{'active':item===curTag}"></topbar-tag>
          </div>
        </div>
        <div class="topbar-search">
          <form action="" class="topbar-search-form">
            <label for="search-btn">
              <svg class="icon icon-search" aria-hidden="true">
                <use xlink:href="#icon-search"></use>
              </svg>
            </label>
            <input type="submit" class="search-btn" id="search-btn" @click="">
            <input type="text"  class="search-text" placeholder="Search" >
          </form>
        </div>
        <div class="topbar-sign">
            <div class="exit">
              <svg class="icon icon-search" aria-hidden="true">
                <use xlink:href="#icon-exit"></use>
              </svg>
            </div>
            <div class="tips">
              <svg class="icon icon-search" aria-hidden="true">
                <use xlink:href="#icon-ring"></use>
              </svg>
            </div>
            <div class = 'info'>
              <img src="http://via.placeholder.com/32x32" alt="">
            </div>
        </div>
      </div>
  </div>
</template>

<script>
  import topbarTag from '../../components/topbar-tag/tag.vue'

    export default {
        data() {
            return {}
        },
        computed:{
          tags(){
            return this.$store.state.topbarTags
          },
          curTag(){
            return this.$store.state.curTag
          }
        },
        components:{
          topbarTag
        },
        methods:{

        }
    }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>
  @import '../../common/stylus/base.styl'
  #topbar
    padding 8px  48px
    .topbar-container
      margin 0 auto
      background-color $ddd
      display flex
      align-items center
      justify-content flex-end
      .tag-area
        width 100%
        display: flex
        justify-content flex-start
        overflow hidden

  .topbar-search-form
    display: flex
    justify-content center
    align-items center
    .search-btn
      display none
    label
      display: flex
      align-items center
      justify-content center
      width 32px
      height 32px
      &:hover
        background-color: #ffe87c

    .search-text
       display: block
       height 32px
       border none
       outline none
       background-color: #eee

  .topbar-sign

    display: flex
    justify-content flex-end
    >div
      margin-left 24px
    .exit,.tips
      display: flex
      align-items center
    .info
      width 32px
      height 32px
      border-radius 50%
      overflow hidden
</style>






