<template>
  <div id="sidebar">
    <div class="sidebar-logo"><h3>Jekyll & Hyde</h3></div>
      <dl>
        <dd>
            <router-link to="/article">
              <div class="main-router" @click="addTag" data-name="article">
                <svg class="icon icon-search" aria-hidden="true">
                  <use xlink:href="#icon-article"></use>
                </svg>
                article
              </div>
          </router-link>
        </dd>
        <dd>
          <router-link to="/portfolio">
            <div class="main-router" @click="addTag" data-name="portfolio">
              <svg class="icon icon-search" aria-hidden="true">
                <use xlink:href="#icon-portfolio"></use>
              </svg>
              portfolio
            </div>
          </router-link>
        </dd>
        <dd>
          <router-link to="/projects">
            <div class="main-router" @click="addTag" data-name=" projects">
              <svg class="icon icon-search" aria-hidden="true">
                <use xlink:href="#icon-projects"></use>
              </svg>
                projects
            </div>
          </router-link>
        </dd>
      </dl>
      <dl>
        <dt>statistic</dt>
        <dd>
          <router-link to="/visit">
            <div class="main-router" @click="addTag" data-name="visits">
              <svg class="icon icon-search" aria-hidden="true">
                <use xlink:href="#icon-statistics"></use>
              </svg>
              visits
            </div>
          </router-link>
      </dd>
    </dl>
      <dl>
        <dt>others</dt>
        <dd>
            <router-link to="about">
              <div class="main-router" @click="addTag" data-name="about">
                <svg class="icon icon-search" aria-hidden="true">
                  <use xlink:href="#icon-about"></use>
                </svg>
                about
              </div>
            </router-link>
        </dd>
        <dd>
          <router-link to="/contact">
            <div class="main-router" @click="addTag" data-name="contact">
              <svg class="icon icon-search" aria-hidden="true">
                <use xlink:href="#icon-contact"></use>
              </svg>
              contact
            </div>
          </router-link>
        </dd>
      </dl>
  </div>
</template>

<script>

    export default {
        data() {
            return {}
        },
      methods:{
          addTag(e){
            this.$store.commit('addTag',e.target.dataset.name)
            this.$store.commit('updateCurTag',e.target.dataset.name)
          },
      }
    }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>
  @import '../../common/stylus/base.styl'
  #sidebar
    width 220px
    min-width 200px
    padding-top 24px
    background-color: $color-sidebar
    color $color-sidebar-text
    font-size $font-size-medium-x
    border-top-right-radius 4px;
    border-bottom-right-radius 4px;
  .sidebar-logo
    text-align center
    font-size $font-size-large-x
    font-weight bold
    margin-bottom 20px
  dl
    border-bottom 1px solid #161e25
    dt
      padding 15px 25px
    a
      text-decoration none
    .main-router
      color #a7c6dc
      display: flex
      align-items center
      padding 15px 0px 15px 25px
      &:hover
        background-color: #152028
      .icon
        width 16px
        height 16px
        fill #a7c6dc
        margin-right 16px


</style>



